#foody {
  background-image: url("../img/foody.webp");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat; }

.tabs {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr); }
  .tabs button {
    background-color: #f5edd8;
    color: #8ead2f;
    font-size: 3.5rem;
    border: none;
    border-radius: 1rem;
    transition: all 500ms;
    padding: 1rem;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    margin-bottom: 1rem; }
    .tabs button.actual {
      background-color: #ff4747;
      color: #f5edd8; }
    .tabs button:hover {
      background-color: #ff4747;
      color: #f5edd8;
      transform: scale(1.05);
      cursor: pointer; }
    .tabs button:first-child {
      margin-left: 2rem; }
      @media (min-width: 768px) {
        .tabs button:first-child {
          margin-left: 15rem; } }
    .tabs button:last-child {
      margin-right: 2rem; }
      @media (min-width: 768px) {
        .tabs button:last-child {
          margin-right: 15rem; } }

.lista-foody {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  overflow: hidden; }
  @media (min-width: 768px) {
    .lista-foody {
      display: grid;
      gap: 2rem;
      grid-template-columns: repeat(3, 1fr); } }
  @media (min-width: 1024px) {
    .lista-foody {
      display: grid;
      gap: 2rem;
      grid-template-columns: repeat(4, 1fr); } }
  .lista-foody .div-comida {
    margin: 0px auto;
    text-align: center;
    transition: all 300ms;
    height: fit-content;
    cursor: pointer;
    overflow: hidden;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
    @media (min-width: 768px) {
      .lista-foody .div-comida:hover {
        transform: scale(1.02);
        overflow: hidden; } }
    .lista-foody .div-comida.selected {
      background-color: #ff4747; }
      .lista-foody .div-comida.selected .boton-menu {
        background: #f5edd8;
        color: #575757; }
      .lista-foody .div-comida.selected .cantidad-menu {
        color: #f5edd8; }
    .lista-foody .div-comida .nombre-comida {
      display: block;
      background-color: #f5edd8;
      color: #8ead2f;
      font-size: 5rem;
      border-top: 0.7rem solid #ff4747;
