      margin-top: 1rem;
      margin-bottom: 1rem; }
    .lista-foody .div-comida .precio-comida {
      color: #f5edd8;
      margin: 1rem;
      font-family: "mano";
      width: 13rem;
      height: 13rem;
      -moz-border-radius: 50%;
      -webkit-border-radius: 50%;
      border-radius: 50%;
      background: #ff4747;
      line-height: 13rem;
      font-size: 5rem;
      font-family: "century";
      font-weight: bolder; }
    .lista-foody .div-comida img {
      width: 55%;
      margin-bottom: 0rem;
      position: relative;
      top: -7rem;
      left: 5rem; }
    .lista-foody .div-comida .boton-menu {
      width: 5rem;
      height: 5rem;
      color: #f5edd8;
      background: #575757;
      border: none;
      cursor: pointer;
      border-radius: 50%;
      font-family: Arial, Helvetica, sans-serif;
      font-size: 2.5rem;
