      transition: all 200ms;
      margin-right: .3rem;
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
      .lista-foody .div-comida .boton-menufocus {
        outline: 0; }
      .lista-foody .div-comida .boton-menu:hover {
        transform: scale(1.14);
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
      @media (min-width: 768px) {
